"use server";
import bcrypt from "bcrypt";
import prisma from "../../../prisma/seed.";
import jwt from "jsonwebtoken";
import { cookies } from "next/headers";

type ErrorsStateValidator = {
  hasUser?: boolean;
  passwordLength?: boolean;
  propertyExisted?: boolean;
};

export default async function signup(
  state: { errors: ErrorsStateValidator },
  fd: FormData
): Promise<typeof state> {
  const errors: ErrorsStateValidator = {} as ErrorsStateValidator;
  const data = {
    username: fd.get("username") as string,
    password: fd.get("password") as string,
    email: fd.get("email") as string,
  };
  if (!data.username && !data.password & !data.email) {
    errors = { ...errors, propertyExisted: true };
  } else if (data.password <= 5) {
    errors = { ...errors, passwordLength: true };
  }
  const findUser = await prisma.user.findFirst({
    where: {
      OR: [{ username: data.username }, { email: data.email }],
    },
  });

  if (findUser) {
    errors = { ...errors, hasUser: true };
  } else {
    const hashPassword = await bcrypt.hash(data.password, 10);
    await prisma.user.create({
      data: {
        ...data,
        password: hashPassword,
      },
    });
    console.log(2);
    const jwtKey = await jwt.sign(data.password, process.env.SECRET_KEY!);
    const cookie = await cookies();
    cookie.set("jwt", jwtKey);
    errors = { ...errors, propertyExisted: true };
  }
  return errors;
}
